
import axios from 'uni-request';
export default {
	onLaunch: function() {
		uni.showLoading({
		    title: '加载中',
			mask:true
		});
		let self = this;
		console.log('App Launch')
		let token = uni.getStorageSync('token')||''
		console.log(token)
		if(token){
			self.getStatus()
		}else{
			wx.login({
			  success (res) {
			    if (res.code) {
					console.log(self)
					self.$api.post('api/user/login',{
						code:res.code
					}).then((res)=>{
						if(res.access_token){
							self.hasToken=true
						}else{
							return
						}
						
						if (!res.first) {
							uni.setStorageSync('token',res.access_token)
							uni.setStorageSync('user_id',res.user_id)
							self.getStatus()
						}else{
							
						}
					})
			    } else {
			      console.log('登录失败！' + res.errMsg)
			    }
			  },
			  fail(){
				  uni.hideLoading();
			  }
			})
		}
		// wx.loadFontFace({
		//   family: 'book',
		//   global:true,
		//   source: 'url("https://www.csdc8.top/mini/book3500.ttf")',
		//   success: res => {
		//     console.log('font load success', res)
		//   },
		//   fail: err => {
		//     console.log('font load fail', err)
		//   }
		// })
		
	},
	onShow: function() {
		this.autoUpdate()
		console.log('App Show')
		//this.getInfo()
	},
	onHide: function() {
		console.log('App Hide')
	},
	methods:{
		getStatus(){
			this.$api.get('api/pray/show').then((res)=>{
				console.log(res)
				uni.setStorageSync('paryData',res.prayer)
				if(res.status==1){
					uni.redirectTo({
					    url:'/pages/package/package?detail='+JSON.stringify(res.dojo)+'&status=1'
					});
				}else if(res.status==0){
					uni.redirectTo({
						url:'/pages/package/package?detail='+JSON.stringify(res.dojo)+'&expired=1'
					})
				}
				uni.hideLoading();
			})
		},
		//获取用户信息
		getInfo(){
			let token=uni.getStorageSync('token')
			console.log(token)
			if(token){
				axios.post('user/getMine',{token}).then((data)=>{
					console.log(data)
					let info=data.user;
					if(uni.getStorageSync('userInfo')){
						let oldInfo=JSON.parse(uni.getStorageSync("userInfo"));
							if(oldInfo.amount!==info.amount){
								uni.showTabBarRedDot({
									index:2
								})
								uni.setStorageSync('amountChange','1')
							}else{
								uni.setStorageSync('amountChange','0')
							}
						uni.setStorageSync('userInfo', JSON.stringify(info));
					}
				})
			}else{
				// uni.navigateTo({
				// 	url:"/pages/authorize/authorize"
				// })
			}
		},
		autoUpdate:function(){
			console.log(new Date())
			var self=this
			// 获取小程序更新机制兼容
			if (wx.canIUse('getUpdateManager')) {
			  const updateManager = wx.getUpdateManager()
			  //1. 检查小程序是否有新版本发布
			  updateManager.onCheckForUpdate(function (res) {
				// 请求完新版本信息的回调
				if (res.hasUpdate) {
				  //2. 小程序有新版本，则静默下载新版本，做好更新准备
				  updateManager.onUpdateReady(function () {
					console.log(new Date())
					wx.showModal({
					  title: '更新提示',
					  content: '新版本已经准备好，是否重启应用？',
					  success: function (res) {
						if (res.confirm) {
						  //3. 新的版本已经下载好，调用 applyUpdate 应用新版本并重启
						  updateManager.applyUpdate()
						} else if (res.cancel) {
						  //如果需要强制更新，则给出二次弹窗，如果不需要，则这里的代码都可以删掉了
						  wx.showModal({
							title: '温馨提示~',
							content: '本次版本更新涉及到新的功能添加，旧版本无法正常访问的哦~',
							success: function (res) {     
							  self.autoUpdate()
							  return;                 
							  //第二次提示后，强制更新                      
							  if (res.confirm) {
								// 新的版本已经下载好，调用 applyUpdate 应用新版本并重启
								updateManager.applyUpdate()
							  } else if (res.cancel) {
								//重新回到版本更新提示
								self.autoUpdate()
							  }
							}
						  })
						}
					  }
					})
				  })
				  updateManager.onUpdateFailed(function () {
					// 新的版本下载失败
					wx.showModal({
					  title: '已经有新版本了哟~',
					  content: '新版本已经上线啦~，请您删除当前小程序，重新搜索打开哟~',
					})
				  })
				}
			  })
			} else {
			  // 如果希望用户在最新版本的客户端上体验您的小程序，可以这样子提示
			  wx.showModal({
				title: '提示',
				content: '当前微信版本过低，无法使用该功能，请升级到最新微信版本后重试。'
			  })
			}
		  }
		  
	}
}
